#Contact .social_icon {
    margin-right: 5px;
}

#Contact .gitlab {
    height: 50px;
    margin-right: 5px;
    text-decoration: none;
}

#Contact h2 {
    color: black;
}

#Contact span {
    color: black;
}

#Contact .link {
    color: black;
    position: relative;
    text-decoration: none;
    cursor: pointer;
}

#Contact .link:hover::after {
    width: 100%;
    background: black;
}

#Contact .link:after {
    position: absolute;
    bottom: -3px;
    left: 0;
    content: "";
    width: 0;
    height: 1px;
    transition: all 0.2s linear;
}

/* light and dark mode */
.light_mode_contact {
    background: white;
}

.dark_mode_contact {
    background: #c8c8c8;
}
